x;justify-content:center;align-items:center;font-size:2em;font-weight:bold}.login{width:100vw;height:100vh;display:flex;flex-direction:column;justify-content:space-around;overflow:hidden;color:#c9b3b3;background-color:white}.login__login-box{display:flex;align-items:center;position:relative}.login__login-span{font-size:2.8rem;font-weight:400;margin-left:14%;position:relative}.login__login-span::before{content:"";position:absolute;top:50%;left:-35px;transform:translateY(-50%);width:25px;height:25px;background-color:#ffc244;display:inline-block;border-radius:50%;box-shadow:4px 0 20px -5px rgba(0,0,0,0.75)}.login__boubles{width:50px;height:50px;border-radius:50%;background-color:#ffc244}.login__boubles--one{position:absolute;right:60px;box-shadow:-8px 0 20px -12px rgba(0,0,0,0.75)}.login__boubles--two{width:90px;height:90px;position:absolute;right:-45px;box-shadow:-5px 3px 20px -12px rgba(0,0,0,0.75)}.login__boubles--three{width:90px;height:90px;position:absolute;left:-45px;box-shadow:5px -3px 20px -12px rgba(0,0,0,0.75)}.login__boubles--four{position:absolute;left:60px;box-shadow:8px 0 20px -12px rgba(0,0,0,0.75)}.login__signUp-box{display:flex;align-items:center;position:relative;justify-content:flex-end}.login__signUp-span{font-size:2.5rem;font-weight:400;margin-right:14%;position:relative}.login__signUp-span::after{content:"";position:absolute;top:50%;right:-35px;transform:translateY(-50%);width:25px;height:25px;background-color:#ffc244;display:inline-block;border-radius:50%;box-shadow:-4px 0 20px -5px rgba(0,0,0,0.75)}.login__form-wrapper{width:100%;height:60%;display:flex;flex-direction:column;justify-content:space-evenly;align-items:center}.login__inputs-box{width:92%;height:30%;margin-bottom:10px;display:flex;flex-direction:column;justify-content:space-evenly;align-items:center;border-radius:8px;background-color:#fff}.login__username-wrapper,.login__password-wrapper{position:relative;display:flex;justify-content:center}.login__username-icon,.login__password-ico